import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 购物车
    cart:localStorage.getItem("cart")?JSON.parse(localStorage.getItem("cart")) : [],
    // 活跃的地址
    chooseAddressId:""
  },
  getters: {
    order(state){
      return state.cart.filter(v=>v.checked)
    },
    orderPrice(state,getters){
      let total = 0;
      getters.order.forEach(v=>{
        total += v.count *v.price;
      });
      return total;
    }
  },
  mutations: {
    addToCart(state,payLoad){
      state.cart.push(payLoad);
      localStorage.setItem("cart",JSON.stringify(state.cart))
    },
    removeFromCart(state,id){
       state.cart = state.cart.filter(v=>v.id*1!==id*1) ;
       localStorage.setItem("cart",JSON.stringify(state.cart))
    },
    countChange(state){
      localStorage.setItem("cart",JSON.stringify(state.cart))
    },
    
    chooseAddress(state,payLoad){
      state.chooseAddressId = payLoad;
    },
    // 订单提交成功
    removeCheckGoodsFromCart(state){
      state.cart = state.cart.filter(v=>!v.checked);
      localStorage.setItem("cart",JSON.stringify(state.cart))
    }
  },
  actions: {
  },
  modules: {
  }
})
