import Vue from 'vue'
import App from './App.vue'
import Router from 'vue-router'
import router from './router'
import store from './store'
// import axios from 'axios'
// import Vant from 'vant';
import axios from "./api/index"
import api from "./api/api.js"
// 引入视频播放
import VueVideoPlayer from 'vue-video-player'
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')

import FilterMethods from "@/filters" // 格式化日期和货币的
import directives from "./directives/index"
// 按需引入vant组件
import 'vant/lib/index.css';
import { Toast,Button,Swipe, SwipeItem,Sku,Field,Cell, CellGroup,AddressEdit,Area,AddressList,Overlay  } from 'vant';

Vue.use(Sku);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Toast);
Vue.use(Button);
Vue.use(Field);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(AddressEdit);
Vue.use(Area);
Vue.use(AddressList);
Vue.use(Overlay);

Vue.config.productionTip = false;
Vue.prototype.$http = axios;
Vue.prototype.$api = api;

for(let i in directives){
  Vue.directive(i,directives[i]);
}

Vue.use(axios)
Vue.use(FilterMethods); // 过滤货币
// Vue.use(Vant);   // 导入vant
Vue.use(VueVideoPlayer) // 视频播放
new Vue({
  router,
  store,
  axios,
  render: h => h(App)
}).$mount('#app')

// 解决多次点击同一个报错
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
